import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    windowWidth: window.screen.width,
    windowHeight: window.screen.height,
    CurrentNavList: [
      {
        id: 1,
        name: ""
      },
      {
        id: 2,
        name: ""
      },
      {
        id: 3,
        name: ""
      },
      {
        id: 4,
        name: ""
      },
      {
        id: 5,
        name: ""
      },
      {
        id: 6,
        name: ""
      },
      {
        id: 7,
        name: ""
      },
      {
        id: 8,
        name: ""
      },
      {
        id: 9,
        name: ""
      }
    ],
    NavList: [
      {
        id: 1,
        name: ""
      },
      {
        id: 2,
        name: ""
      },
      {
        id: 3,
        name: ""
      },
      {
        id: 4,
        name: ""
      },
      {
        id: 5,
        name: ""
      },
      {
        id: 6,
        name: ""
      },
      {
        id: 7,
        name: ""
      },
      {
        id: 8,
        name: ""
      },
      {
        id: 9,
        name: ""
      },
      {
        id: 10,
        name: ""
      },
      {
        id: 11,
        name: ""
      },
      {
        id: 12,
        name: ""
      },
      {
        id: 13,
        name: ""
      },
      {
        id: 14,
        name: ""
      },
      {
        id: 15,
        name: ""
      },
      {
        id: 16,
        name: ""
      },
      {
        id: 17,
        name: ""
      },
      {
        id: 18,
        name: ""
      },
      {
        id: 19,
        name: ""
      },
      {
        id: 20,
        name: ""
      },
      {
        id: 21,
        name: ""
      },
      {
        id: 22,
        name: ""
      },
      {
        id: 23,
        name: ""
      },
      {
        id: 24,
        name: ""
      }
    ]
  },
  getters: {
    //  store 
    unselected: state => {
      //  state 
      let arr = [];
      let bl = null;
      state.NavList.forEach(s => {
        bl = true;
        state.CurrentNavList.forEach(i => {
          if (i.id == s.id) {
            bl = false;
          }
        });
        if (bl) {
          arr.push(s);
        }
      });
      return arr;
    }
  },
  mutations: {
    // mutation 
    del(state, id) {
      state.CurrentNavList = state.CurrentNavList.filter(i => {
        return i.id !== id;
      });
    },
    add(state, id) {
      let item = state.NavList.filter(i => {
        return i.id == id;
      });
      state.CurrentNavList.push(item[0]);
    }
  },
  actions: {}
});
